.searchBar {
  margin-top: 30px;
}

.input {
  color: #171718;
  font-size: 16px;
  min-height: 38px;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid rgb(179, 179, 179);
  width: 100%;
  outline: 0;
  transition: border-color 200ms ease-in-out;
  background: url(../../img/search-black-24dp.svg) no-repeat 98% 50%;
}

.input:focus {
  border-width: 2px;
  border-color: rgb(38, 132, 255);
}
