* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

.currencyList {
  margin: 30px auto 30px;
}

.list {
  width: 250px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.listItem {
  font-size: 24px;
  margin-bottom: 10px;
}

.listItem::after {
  display: block;
  content: '';
  height: 2px;
  width: 100%;
  background-color: rgb(161, 161, 161);
}
